@import '~antd/dist/antd.css';
.app {
  min-height: 100vh;
  background-color: #fbfbfb;
}
.app-skeleton__spin-section {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.log-in {
  display: flex;
  align-items: center;
  justify-content: center;
}
.log-in-card {
  width: 450px;
  text-align: center;
  padding: 10px 0 20px;
}
.log-in-card__google-button {
  margin: 40px auto;
  border-radius: 2px;
  background-color: #4285f4;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.24), 0 0 1px 0 rgba(0, 0, 0, 0.12);
  border: none;
  display: flex;
  align-items: center;
  padding: 1px;
  color: #fff;
  cursor: pointer;
}
.log-in-card__google-button-logo {
  height: 43px;
  background-color: #fff;
}
.log-in-card__google-button-text {
  text-align: left;
  white-space: nowrap;
  padding: 10px;
}
